.card-centerpiece {
  position: relative;
  grid-column: 2 / 4;
  grid-row: 2;
  /*border: solid rgba(173,216,230,0.2);*/
  height: 250px;
  width: 500px;
  display: grid;
  justify-content: center;
}

.centerpiece-icon {
  width: 140px;
  text-align: center;
  font-size: 5em;
  color: #ADD8E6;
  opacity: 1;
  margin: 1.7rem auto;
}

.centerpiece-text {
  font-family: Andale Mono,AndaleMono,monospace;
  color: #ADD8E6;
  font-size: 1.7em;
  text-align: center;
  padding: 10px 20px 10px 20px;
  text-transform: uppercase;
  margin-top: 0;
  margin-bottom: 0;
  /*opacity: 0.7;*/
}

.centerpiece-fav-text {
  font-family: Andale Mono,AndaleMono,monospace;
  color: #ADD8E6;
  text-align: center;
  margin-top: 0;
  text-transform: uppercase;
}

.fav-total-wrapper {
  float: right;
  display: flex;
  width: 50px;
  position: absolute;
  right: 1rem;
  top: 1rem;
}

.fav-count-display {
  margin: 0;
  padding-left: 1rem;
}

.fav-count-display,
.total-fav-icon {
  color: #ADD8E6;
}

@media (max-width: 1000px) {
  .card-centerpiece {
    grid-column: 2 / 3;
    width: 250px;
  }
}

@media (max-width: 750px) {
  .card-centerpiece {
    grid-column: 1 / 3;
    width: 500px;
  }
}

@media (max-width: 500px) {
  .card-centerpiece {
    grid-column: 1;
    width: 250px;
  }
}