.error-page {
  margin: auto;
  width: 350px;
}

.card-container .error-page {
  margin-top: 2rem;
  grid-column: 1/3;
}

.error {
  overflow: hidden;
  width: 350px;
  margin: auto;
  position: relative;
  text-transform: uppercase;
  color: #ADD8E6;
  font-family: "Lucida Console", Monaco, monospace;
  text-align: center;
}

.error:before, .error:after {
  content: 'CONNECTION LOST...';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  background: #000;
}

.error:before {
  -webkit-animation: glitch 1.5s linear infinite;
          animation: glitch 1.5s linear infinite;
  -webkit-animation-name: glitch;
  -webkit-animation-duration: 1.5s;
  -webkit-animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
  
  left: 8px;
  text-shadow: 2px 0 #ADD8E6;
}

.error:after {
  
  -webkit-animation: glitch 2s linear infinite;
  
          animation: glitch 2s linear infinite;
  
  -webkit-animation-name: glitch;
  -webkit-animation-duration: 2s;
  -webkit-animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
  
  left: 4px;
  text-shadow: -2px 0 #FE3A7F;
}

@keyframes glitch {
  0% {
    clip: rect(10px, 350px, 11px, 0);
  }
