.card-wrapper {
  background: #000;
  width: 250px;
  height: 250px;
  font-family: sans-serif;
}

.card-wrapper:hover {
  cursor: pointer;
}

.figure {
  overflow: hidden;
  position: relative;
  width: 100%;
  height: 100%;
}

.card-image {
  width: 250px;
  height: 250px;
  transition: .5s;
  object-fit: cover;
}

.figure .caption {
  display: grid;
  align-items: center;
  position: absolute;
  top: 15px;
  left: 15px;
  right: 15px;
  bottom: 15px;
}

.marked-icon-wrapper .fa-journal-whills {
  position: absolute;
  top: 10px;
  left: 10px;
  font-size: 25px;
  opacity: 0;
  color: #ADD8E6;
  z-index: 2;
  transition: .5s;
}

.marked-icon-wrapper .favorited {
  opacity: 1;
}

.figure:hover .marked-icon-wrapper .fa-journal-whills {
  opacity: 0;
}

.caption:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-top: 1px solid #ADD8E6;
  border-bottom: 1px solid #ADD8E6;
  box-sizing: border-box;
  transition: .5s;
  -webkit-transform: scale(0, 1);
          transform: scale(0, 1);
  z-index: 1;
}

.caption:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-left: 1px solid #ADD8E6;
  border-right: 1px solid #ADD8E6;
  box-sizing: border-box;
  transition: .5s;
  -webkit-transform: scale(1, 0);
          transform: scale(1, 0);
  z-index: 1;
}

.caption.favorited:after,
.caption.favorited:before {
  box-shadow: 2px 5px 5px #fff;
}

.caption .card-content,
.card-title {
  padding: 0 10px 0;
  opacity: 0;
  transition: .5s;
  text-align: center;
  z-index: 2;
}

.card-title {
  color:#ADD8E6;
  margin: 0;
  padding: 0;
  font-size: 17px;
  text-transform: uppercase;
  height: 17px;
}

.caption .card-content p {
  color: #ADD8E6;
  padding: 0;
  font-size: 14px;
}

.favorite-icon {
  color: #ADD8E6;
  font-size: 30px;
}

.icon-wrapper {
  width: 35px;
  height: 35px;
  margin: auto;
}

.card-wrapper:hover .caption:after {
  -webkit-transform: scale(1, 1);
          transform: scale(1, 1);
}

.card-wrapper:hover .card-content,
.card-wrapper:hover .card-title {
  opacity: 1;
}

.card-wrapper:hover .card-image {
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
  opacity: .4;
}

.card-wrapper:hover .caption:before {
  -webkit-transform: scale(1, 1);
          transform: scale(1, 1);
}
